export default {
	data() {
		return {
			time: "12:30",
			number: 0,
			u_id: 0,
			name: '',
			industry: '',
			butt: '',
			phone: 0,
			post: '',
			new: '',
			scale: [],
			scaleIndex: 0,
			scale_id: {},
			area: [],
			areaIndex: 0,
			area_id: {},
			ImgUrl: this.ImgUrl
		}
	},
	methods: {
		// 获取区域
		getArea() {
			let baseUrl = this.WebUrl
			let that = this
			uni.request({
				url: baseUrl + '/getArea',
				success: (res) => {
					res.data.data.forEach(function(item){
						that.area.push(item.dict_name)
						that.area_id[item.dict_name] = item.dict_id
					})
				}
			})
		},
		
		// 获取规模
		getScale() {
			let baseUrl = this.WebUrl
			let that = this
			uni.request({
				url: baseUrl + '/getScale',
				success: (res) => {
					console.log(res)
					res.data.data.forEach(function(item){
						that.scale.push(item.s_code)
						that.scale_id[item.s_code] = item.scale_id
					})
				}
			})
		},
		
		areachange(e) {
			this.areaIndex = e.target.value
		},
		
		nameValue(e) {
			this.name = e.target.value
		},
		
		industryValue(e) {
			this.industry = e.target.value
		},
		
		scaleChange(e) {
			this.scaleIndex = e.target.value
		},
		
		buttValue(e) {
			this.butt = e.target.value
		},
		
		phoneValue(e) {
			this.phone = e.target.value
		},
		
		postValue(e) {
			this.post = e.target.value
		},
		
		newValue(e) {
			this.new = e.target.value
		},
		
		addEnter() {
			let baseUrl = this.WebUrl
			let that = this
			let areaId = this.area_id[this.area[this.areaIndex]]
			let scale = this.scale_id[this.scale[this.scaleIndex]]
			if (this.name === ''){
				uni.showModal({
					title: "提示",
					content: "请输入单位名称",
					showCancel: false
				})
			}else if (this.industry === ''){
				uni.showModal({
					title: "提示",
					content: "请输入行业",
					showCancel: false
				})
			}else if (this.butt === ''){
				uni.showModal({
					title: "提示",
					content: "请输入对接人名字",
					showCancel: false
				})
			}else if (!(/^1[3456789]\d{9}$/.test(this.phone))){
				uni.showModal({
					title: "提示",
					content: "手机号码有误，请重新输入",
					showCancel: false
				})
			}else if (this.post === ''){
				uni.showModal({
					title: "提示",
					content: "请输入职业",
					showCancel: false
				})
			}else if (this.new === ''){
				uni.showModal({
					title: "提示",
					content: "请输入公司目前所在地",
					showCancel: false
				})
			}else {
				uni.getStorage({
					key: 'u_id',
					success:function(res) {
						let u_id = res.data
						
						uni.request({
							url: baseUrl + '/addEnter',
							method: 'POST',
							data: {
								area_id: areaId,
								u_id: u_id,
								name: that.name,
								industry_id: that.industry,
								scale_id: scale,
								butt_joint: that.butt,
								phone: that.phone,
								post: that.post,
								address: that.new
							},
							success: (res) => {
								console.log(res)
								uni.showModal({
									title: "提示",
									content: res.data.msg,
									showCancel: false,
									success: (resd) => {
										if (resd.confirm && res.data.msg === '请求成功!') {
											uni.navigateBack({
												delta: 1
											})
										}
									}
								})
							}
						})
					}
				})
				
			}
		}
	},
	onLoad: function(){
		this.getArea()
		
		this.getScale()
	}
}
